import { Component, OnInit, Input, OnChanges, Output, EventEmitter  } from '@angular/core';
import { SessionService } from '../session.service';
import { Login } from '../login';
import { Router } from '@angular/router';

@Component({
  selector: 'app-structure',
  templateUrl: './structure.component.html',
  styleUrls: ['./structure.component.css']
})
export class StructureComponent implements OnInit, OnChanges {

 constructor(private sessionService:SessionService, private router:Router){}
 isLoggedIn:boolean=false;
 activeComponent:boolean=false;
 @Output() loginEvent = new EventEmitter<boolean>();
 routerConfig:any={about:false,getAccess:false,dashboard:false,login:false};
  getUser:Login = this.sessionService.getUserSession();
  router_Url:string;
  @Input() headerClick:number;
  receiveLoginEvent($event){
	  this.isLoggedIn=$event;
	  this.customrouter();
	  this.loginEvent.emit(this.isLoggedIn);
  }
  ngOnInit() {
	  this.customrouter();
	  
  }
  ngOnChanges(changes){
	  
	  this.customrouter();
  }
  customrouter(){
	 
	  if(this.router.url=="/"){
		  if(this.isLoggedIn){
			  this.routerConfig={about:false,getAccess:false,dashboard:true,login:false};
		  }else{
		  this.routerConfig={about:false,getAccess:false,dashboard:false,login:true};
		  }
	  }else 
	  if(this.router.url=="/dashboard"){
		  this.routerConfig={about:false,getAccess:false,dashboard:true,login:false};
	  }else
	  if(this.router.url=="/about"){
		  this.routerConfig={about:true,getAccess:false,dashboard:false,login:false};
	  }else
	  if(this.router.url=="/getAccess"){
		  this.routerConfig={about:false,getAccess:true,dashboard:false,login:false};
	  }
	  
	  if(this.getUser!== null && this.getUser.userlogged===true){
	   this.isLoggedIn=true;
	   }
  }

}
