import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule,ReactiveFormsModule}   from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { AppRoutingModule } from './app-routing.module';
import { StorageServiceModule } from 'angular-webstorage-service';
import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { UserComponent } from './user/user.component';
import { ApplinksComponent } from './applinks/applinks.component';
import { DllistsComponent } from './dllists/dllists.component';
import { ServerlistComponent } from './serverlist/serverlist.component';
import { TeamhierarchyComponent } from './teamhierarchy/teamhierarchy.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatAutocompleteModule,MatPaginatorModule, MatSortModule, MatTableModule, MatTreeModule, MatIconModule} from '@angular/material';
import { CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { AboutComponent } from './about/about.component';
import { StructureComponent } from './structure/structure.component';
import { AccessComponent } from './access/access.component';
import { TimelineComponent } from './timeline/timeline.component';
import { VendorComponent } from './vendor/vendor.component';
const appRoutes: Routes = [
   { path: 'dashboard/apps', component: ApplinksComponent },
   { path: 'dashboard/dllists', component: DllistsComponent },
   { path: 'dashboard/servers', component: ServerlistComponent },
   { path: 'dashboard/team', component: TeamhierarchyComponent },
   { path: 'dashboard/timeline', component: TimelineComponent },
   { path: 'dashboard/vendor', component: VendorComponent },
   { path: 'about', component: StructureComponent },
   { path: 'getAccess', component: StructureComponent },
   { path: 'dashboard',component: StructureComponent },
   { path: '', component: ApplinksComponent }
  /*{ path: '**', component: HeaderComponent } */
];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    HeaderComponent,
    FooterComponent,
    UserComponent,
    ApplinksComponent,
    DllistsComponent,
    ServerlistComponent,
    TeamhierarchyComponent,
    AboutComponent,
    StructureComponent,
    AccessComponent,
    TimelineComponent,
    VendorComponent
  ],
  imports: [
    BrowserModule,
	FormsModule,
	StorageServiceModule,
    AppRoutingModule,
	MatAutocompleteModule,
	MatPaginatorModule,
	MatSortModule,
	MatTableModule,
	MatTreeModule,
	MatIconModule,
	ReactiveFormsModule,
	RouterModule.forRoot(
      appRoutes,
      { enableTracing: true } // <-- debugging purposes only
    ),
	BrowserAnimationsModule
  ],
  providers: [],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  bootstrap: [AppComponent]
})
export class AppModule { }
