import { Component, OnInit, Input, OnChanges, Output, EventEmitter } from '@angular/core';
import { SessionService } from '../session.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit,OnChanges {

  constructor(private sessionService:SessionService,private router:Router) { }
  isLoggedIn:boolean=false;
  @Input () loginEvent:boolean;
  @Output() headerClickEvent = new EventEmitter<number>();
  ngOnInit() {
	  
	  this.isLoggedIn=this.sessionService.isSessionActive();
	  if(this.isLoggedIn){
		  this.router.navigate(['']);
	  }
	  
  }
  ngOnChanges(changes){
	  
	  this.isLoggedIn=changes.loginEvent.currentValue;
  }
  logOut(){
	  this.sessionService.userLogOut();
	  this.router.navigate(['']);
  }
  switchComponent(){
	  let randomVal= Math.random();
	  this.headerClickEvent.emit(randomVal);
  }

}
