import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Login } from '../login';
import { LoginService } from '../login.service';
import { SessionService } from '../session.service';
import { Router } from '@angular/router';
class Userdata{
}

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private loginService:LoginService, private sessionService:SessionService,private router:Router) { }
  
  userDetails:Login = new Login();
  isLoggedIn:boolean=false;
  login:boolean=true;
  @Output() loginEvent = new EventEmitter<boolean>();
  validateUser():void {
	  if(this.getUser(this.userDetails)){
	 this.loginService.validateUser(this.userDetails).subscribe(userDetails => this.userDetails = userDetails);
	 if(this.userDetails.userlogged===true){
		 this.sessionService.setUserSession(this.userDetails);
		 this.isLoggedIn=true;
		 this.loginEvent.emit(this.isLoggedIn);
	 }
	}
  }
  getUser(userDetails){
	  let getUserData=[{username:'opsteam',passwordis:'opsteam!'},{username:'opsnewteam',passwordis:'opsnewteam!'}];
	  this.login = false;
	  for(let i=0;i<getUserData.length;i++){
		  
		  if(getUserData[i].username === userDetails.username && getUserData[i].passwordis === userDetails.upassword){
			  this.login=true;
			  break;
		  }
	  }
	  this.userDetails.username="";
	  this.userDetails.upassword="";
	  return this.login;
  }
  
  onSubmit(){
	  this.validateUser();
  }
  
  ngOnInit(){
	  
		
  }

}
